
import { defineComponent, ref, watch, getCurrentInstance } from "vue";
import { useTheme } from "/@/plugins/theme";
import { VacantColorType, colorNameGetter, baseColor } from "/@/theme/colors";
import ColorPicker from "./ColorPicker.vue";

export default defineComponent({
  components: {
    ColorPicker,
  },
  setup(props, context) {
    const { changeTheme } = useTheme();
    const colorNames = colorNameGetter();
    const colors = ref(
      colorNames.colorArr.map(({ color, base }) => ({
        name: color,
        color: baseColor[base],
      }))
    );
    const onChange = (colorItem: { name: VacantColorType; color: string }) => {
      const { name } = colorItem;
      changeTheme(name);
      context.emit("change", name);
    };
    return {
      onChange,
      colors,
    };
  },
});
