
import {
  defineComponent,
  reactive,
  toRefs,
  getCurrentInstance,
  ref,
  watch,
  nextTick,
} from "vue";
import { useService } from "@hybridapi/service";
import { useTheme } from "/@/plugins/theme";
import { useI18n } from "vue-i18n";
import { useRoute, useRouter, useHistory } from "/@/router";
import { sleep } from "/@/plugins/resolver";
import { useNextTick } from "/@/plugins/afterTick";

interface Route {
  path: string;
  breadcrumbName: string;
  children?: Array<{
    path: string;
    breadcrumbName: string;
  }>;
}
export default defineComponent({
  setup() {
    const instance: any = getCurrentInstance();
    // get theme
    const { theme } = useTheme(instance);
    // get router
    const routes = ref<Route[]>([]);
    const history = useHistory();
    const route = useRoute();
    const router = useRouter();
    const { t } = useI18n();
    watch(
      () => route.path,
      () => {
        routes.value = history.localRouterState.map((param) => {
          return {
            path: param.path || "",
            breadcrumbName: param.pageName || "",
          };
        });
        console.log(routes.value);
      }
    );
    const { nextTick, nextRender } = useNextTick(instance);
    const onClick = async (item: any, index: number) => {
      console.log("click====", item.routes.length - index - 1);
      for (let i = 0; i < item.routes.length - index - 1; i++) {
        await nextTick();
        router.back();
        await nextRender(1);
        // await sleep(50);
        console.log(item.routes);
        // proxy.$forceUpdate();
      }
      // console.log(index, item);
    };
    return {
      routes,
      onClick,
      theme,
    };
  },
});
