import { router } from './router'
import { RouterState, HistoryType } from './state'
import { routes as configRoutes } from './config'

router.beforeEach((to, from, next) => {
  const $router: any = router;
  console.log($router.routes, $router.options)
  next()
})

// eslint-disable-next-line @typescript-eslint/no-empty-function
// eslint-disable-next-line @typescript-eslint/no-unused-vars
router.afterEach((to, from) => {
  //
  if ((router.options.history as HistoryType).localRouterState && (router.options.history as HistoryType).localRouterState.length > 0) {
    //
  } else {
    if (to.path !== '/') {
      const state: RouterState = { query: to.query, path: to.path, pageName: to.path };
      const configRoute = configRoutes.filter(item => item.path === state.path)
      if (configRoute.length > 0) {
        state.pageName = configRoute[0].pageName
      }
      (router.options.history as HistoryType).localRouterState = [state]
    }
  }
})

export { router as guardRouter }
