
import {
  defineComponent,
  watch,
  getCurrentInstance,
  onMounted,
  onUnmounted,
} from "vue";
import { useStore } from "vuex";
export default defineComponent({
  props: {
    key: String,
  },
  setup(props, context) {
    const instance = getCurrentInstance();
    const { commit } = useStore();
    onMounted(() => {
      if (instance.slots.default) {
        commit("appendFooter", instance.slots.default);
      }
    });
    onUnmounted(() => {
      commit("appendFooter", undefined);
    });
    watch(
      () => props.key,
      () => {
        if (instance.slots.default) {
          commit("appendFooter", instance.slots.default);
        }
      }
    );
    return {};
  },
});
