import { router } from './router'
import { useRouter as vueUseRouter, useRoute, RouterHistory } from 'vue-router'
import { routes as configRoutes } from './config'

export interface RouterState {
  path?: string;
  query?: any;
  new?: boolean;
  pageName?: string;
}

export type HistoryType = RouterHistory & { localRouterState: Array<RouterState> }

function _routerPush(routerArg: RouterState, next?: (routerArg: RouterState) => any) {
  const configRoute = configRoutes.filter(item => item.path === routerArg.path)
  if (configRoute.length > 0 && !routerArg.pageName) {
    routerArg.pageName = configRoute[0].pageName
  }
  if (routerArg.new) {
    this.options.history.localRouterState = [routerArg]
  } else {
    if (!this.options.history.localRouterState) {
      this.options.history.localRouterState = []
    }
    this.options.history.localRouterState.push(routerArg)
  }
  next && next(routerArg)
}

export const routerPush = _routerPush.bind(router)

function _routerBack(routerArg: RouterState, next?: (routerArg: RouterState, preRouterArg: RouterState) => any) {
  if (!this.options.history.localRouterState) {
    this.options.history.localRouterState = []
  }
  this.options.history.localRouterState.pop()
  console.log("this.options.history.localRouterState", this.options.history.localRouterState)
  next && next(routerArg, this.options.history.localRouterState.slice(-1)[0])
}

export const routerBack = _routerBack.bind(router)

export function useHistory() {
  if (!(router.options.history as HistoryType).localRouterState) {
    (router.options.history as HistoryType).localRouterState = []
  }
  return router.options.history as HistoryType
}

export function useRouter() {
  const vueRouter = vueUseRouter()
  return {
    ...vueRouter,
    push(param: RouterState) {
      routerPush(param);
      vueRouter.push({ path: param.path, query: param.query });
    },
    back(param?: RouterState) {
      console.log("router.options.history.localRouterState", (router.options.history as any).localRouterState)
      routerBack(param || {});
      vueRouter.back();
    }
  }
}

export { useRoute }
