import { OutRoute } from './index'

export const home = '/home'

export const routes: Array<OutRoute> = [
  {
    path: '/home',
    pageName: 'menu.home',
    component: () => import('../components/page/Home.vue')
  },
  {
    path: '/mind', // 思维导图
    pageName: 'menu.mind',
    component: () => import('../components/page/FrontendCodeMind.vue'),
    defaultTag: 'frontend',
    tags: [
      {
        key: 'frontend', // 前端思维导图
        name: 'tab.mind.frontend'
      },
    ]
  },
]
