
import {
  defineComponent,
  ref,
  watch,
  onMounted,
  onUpdated,
  getCurrentInstance,
} from "vue";
import { useI18n } from "vue-i18n";
import { useRouter, useRoute } from "/@/router";
import { menus, MenuType } from "/@/router/menu";
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  TranslationOutlined,
  ApiOutlined,
  ApiFilled,
} from "@ant-design/icons-vue";
import { useTheme, useCss } from "/@/plugins/theme";
import PageHeader from "./widgets/PageHeader.vue";
import PageFooter from "./widgets/PageFooter.vue";
import ThemePicker from "./widgets/ThemePicker.vue";
import { MaxWidth } from "/@/GlobalConst";

export default defineComponent({
  components: {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
    TranslationOutlined,
    ApiOutlined,
    ApiFilled,
    PageHeader,
    PageFooter,
    ThemePicker,
  },
  setup() {
    const _MaxWidth = MaxWidth;
    // 获取vue引用
    const instance = getCurrentInstance();
    // 初始化路由
    const router = useRouter();
    const route = useRoute();
    console.log(route.query);
    // 菜单跳转
    const onMenuClick = (menuItem: MenuType) => {
      router.push({
        new: true,
        path: menuItem.route,
        query: {},
      });
    };
    // 多语言切换
    const { proxy } = instance as any;
    const onChangeLang = (lang?: "zh" | "ja" | "en") => {
      if (lang) {
        proxy.$i18n.locale = lang;
      } else {
        if (proxy.$i18n.locale === "zh") {
          proxy.$i18n.locale = "en";
        } else {
          proxy.$i18n.locale = "zh";
        }
      }
    };
    // 生命周期
    onMounted(() => {
      console.log("onMounted");
    });
    onUpdated(() => {
      console.log("onUpdated");
    });
    // 主题切换
    const { theme } = useTheme(instance);
    // 共通样式动态设置
    const { css, reactiveCss } = useCss(instance);
    // css({
    //   ".logo": {
    //     color: "blue !important",
    //   },
    // });
    reactiveCss((theme) => ({
      ".logo": {
        color: `${theme.value.primaryColor} !important`,
      },
    }));
    // 监听菜单被选择
    const selectedKeys = ref<string[]>(["dashboard"]);
    watch(selectedKeys, (newValue, oldValue, clear) => {
      clear(() => {
        console.log("selectedKeys is clear");
      });
    });
    // 菜单主题
    const menuTheme = ref("dark");
    // 监听展开设置宽度 :style="{width:collapsed?'calc(100vw - 80px)':'calc(100vw - 200px)'}"
    // const resetContainerWidth = (isCollapsed) => {
    //   css({
    //     ".layout-right-content .layout-content": {
    //       width: `calc(${_MaxWidth} - ${isCollapsed ? "80px" : "200px"})`,
    //     },
    //   });
    // };
    const collapsed = ref<boolean>(false);
    // resetContainerWidth(collapsed.value);
    reactiveCss(() => {
      return {
        ".layout-right-content .layout-content": {
          width: `calc(${_MaxWidth} - ${collapsed.value ? "80px" : "200px"})`,
        },
      };
    });
    // watch(collapsed, () => {
    //   resetContainerWidth(collapsed.value);
    // });
    // 获取多语言方法
    const { t } = useI18n();
    console.log(t("component.layout.menu.dashboard"));
    return {
      menus,
      menuTheme,
      onMenuClick,
      onChangeLang,
      theme,
      selectedKeys,
      collapsed,
    };
  },
});
