
import {
  defineComponent,
  reactive,
  toRefs,
  getCurrentInstance,
  ref,
  onMounted,
  watch,
  computed,
} from "vue";
import { useService } from "@hybridapi/service";
import { useTheme } from "/@/plugins/theme";
import { useI18n } from "vue-i18n";
/**
 * #param: defaultSelect , tags
 * #event: change
 * #slot:
 */
import { Tag } from "/@/router/index";
export default defineComponent({
  props: {
    defaultSelect: String,
    tags: Array,
    fontSize: String,
    iconSize: String,
  },
  setup(props, context) {
    const instance: any = getCurrentInstance();
    // get theme
    const { theme } = useTheme(instance);
    //
    const tags: any = ref<Tag[]>([]);
    const selectedKeys = ref<string[]>([]);
    const onMenuClick = (tag: Tag) => {
      context.emit("change", { key: tag.key });
    };
    watch(
      () => props.tags,
      () => {
        if (props.tags && props.tags.length > 0) {
          tags.value = props.tags;
          // selectedKeys.value = [(props.tags[0] as Tag).key];
        } else {
          tags.value = [];
        }
      }
    );
    watch(
      () => props.defaultSelect,
      () => {
        if (props.defaultSelect) {
          selectedKeys.value = [props.defaultSelect];
        } else {
          if (props.tags && props.tags.length > 0) {
            selectedKeys.value = [(props.tags[0] as Tag).key];
          } else {
            selectedKeys.value = [""];
          }
        }
      }
    );
    // const tags = computed(() => {
    //   if (props.tags && props.tags.length > 0) {
    //     return props.tags;
    //   } else {
    //     return [];
    //   }
    // });
    // });
    return {
      // eslint-disable-next-line vue/no-dupe-keys
      tags,
      onMenuClick,
      selectedKeys,
      theme,
    };
  },
});
