import { Module } from 'vuex'
import { RootState } from '..'

export interface State {
  selectedTag: { path: string, key: string },
  appendFooterComponent: () => Array<any>
}

const layout: Module<State, RootState> = {
  state: {
    selectedTag: { path: "", key: "" },
    appendFooterComponent: () => []
  },
  // reducer
  mutations: {
    pageTagSelect: (state, value) => {
      state.selectedTag = value
    },
    appendFooter: (state, value) => {
      state.appendFooterComponent = value || (() => [])
    }
  }
}

export default layout
