
import {
  defineComponent,
  ref,
  watch,
  getCurrentInstance,
  onMounted,
} from "vue";
import { useRefresh } from "/@/plugins/afterTick";

export default defineComponent({
  props: {
    colors: Array, // [ { color: '#ffffff', name: 'white' } ]
    select: String, // color.name
  },
  setup(props, context) {
    const instance = getCurrentInstance();
    const refresh = useRefresh(instance);
    const selectedColor = ref("");
    const colorBox: any = {};
    for (const item of props.colors || []) {
      colorBox[(item as any).name + "Ref"] = ref();
    }
    onMounted(() => {
      //
    });
    watch(
      () => props.select,
      () => {
        if (props.select) {
          selectedColor.value = props.select;
          clearSelect();
          colorBox[props.select + "Ref"].value.className += " selected";
        }
      }
    );
    const clearSelect = () => {
      Object.values(colorBox).forEach((colorRef: any) => {
        colorRef.value.className = colorRef.value.className
          .replace("selected", "")
          .trim();
      });
    };
    const onChange = ({ name }, event) => {
      clearSelect();
      event.target.className += " selected";
      const selItem = props?.colors?.filterOne(
        (item: any) => item.name === name
      );
      // console.log(selItem, props?.colors, name);
      context.emit("change", selItem);
      // refresh();
    };
    return {
      selectedColor,
      onChange,
      ...colorBox,
    };
  },
});
