
import { defineComponent, ref, watch, getCurrentInstance } from "vue";
import Nanigation from "./Navigation.vue";
import PageTags from "./PageTags.vue";
import { Tag, useRoute } from "/@/router";
import { routes } from "/@/router/config";
import { useStore } from "vuex";
import { useTheme } from "/@/plugins/theme";

export default defineComponent({
  props: {},
  components: {
    Nanigation,
    PageTags,
  },
  setup() {
    const instance: any = getCurrentInstance();
    // get theme
    const { theme } = useTheme(instance);
    const tags = ref<Array<Tag> | undefined>([]);
    const defaultSelect = ref<string | undefined>("");
    const pageName = ref<string>("");
    const route = useRoute();
    // const path = ref<string>(route.path);
    watch(route, () => {
      const a = route;
      console.log(a);
      const oneTabs = routes.filter(({ path }) => path === route.path);
      if (oneTabs.length > 0) {
        tags.value = oneTabs[0].tags;
        defaultSelect.value = oneTabs[0].defaultTag;
        pageName.value = oneTabs[0].pageName;
      } else {
        // 页面不存在，需要重定向跳转
        tags.value = [];
        defaultSelect.value = "";
        pageName.value = "";
      }
    });
    const { commit } = useStore();
    const onPageTageChange = ({ key }: any) => {
      commit("pageTagSelect", { path: route.path, key });
    };
    return {
      pageName,
      tags,
      defaultSelect,
      onPageTageChange,
      theme,
    };
  },
});
