import { watch, ref, App, nextTick, inject, getCurrentInstance } from 'vue'
import { themes, VacantColorType, ThemeColorType } from '../theme/colors'
import postcss from 'postcss'
import postcssJs from 'postcss-js'
import { createCss, insertCss } from './insertCss'
import { nanoid } from 'nanoid'

type ThemeStyleType = { value: ThemeColorType }

const defaultThemeType: VacantColorType = 'orange'
let app: App
export class Theme {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  install(vue: App, _options: any) {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    app = vue
    vue.config.globalProperties.$themeType = ref(defaultThemeType)
    vue.config.globalProperties.$theme = ref((themes as any)[defaultThemeType])
    watch(vue.config.globalProperties.$themeType, (newValue: string) => {
      vue.config.globalProperties.$theme.value = (themes as any)[newValue]
    })
    vue.provide('theme', vue.config.globalProperties.$theme)
  }
}

export const useTheme = (instance?: ReturnType<getCurrentInstance> | 'global') => {
  // const vue = require('vue')
  let proxy = undefined as any
  if (instance) {
    if (instance === 'global') {
      proxy = (app._instance as any).proxy
    } else {
      proxy = instance.proxy
    }
  }
  // const { proxy } = instance as any;
  return {
    theme: inject('theme') as ThemeStyleType,
    changeTheme: (themeType: VacantColorType) => {
      app.config.globalProperties.$themeType.value = themeType
      if (proxy) {
        nextTick(() => {
          proxy.$forceUpdate() // 单节点刷新
          // (app._instance as any).proxy.$forceUpdate() // 全局刷新
        })
      }
    }
  }
}

export const useCss = (instance: ReturnType<getCurrentInstance> | 'global' | undefined = undefined, styleId: string = nanoid()) => {
  let proxy = undefined as any
  if (instance) {
    if (instance === 'global') {
      proxy = (app._instance as any).proxy
    } else {
      proxy = instance.proxy
    }
  }
  // const { proxy } = instance as any;
  const symbol = Symbol(styleId)
  const styleElement = createCss({ styleId, symbol })
  const css = (style: { [key: string]: any }) => {
    postcss().process(style, { parser: postcssJs }).then((result) => {
      insertCss(result.css, styleElement)
    }).catch(err => {
      console.log(err)
    })
  }
  return {
    css,
    reactiveCss(makeStyle: { [key: string]: any } | ((theme: ThemeStyleType) => any)) {
      const setCss = () => {
        if (typeof makeStyle === 'function') {
          const style = makeStyle(app.config.globalProperties.$theme)
          css(style)
        } else {
          css(makeStyle)
        }
      }
      setCss()
      watch(app.config.globalProperties.$themeType, () => {
        setCss()
        if (proxy) {
          nextTick(() => {
            proxy.$forceUpdate()
          })
        }
      })
    }
  }
}
