import { nextTick } from 'vue'
import { sleep } from './resolver'

export const useNextTick = ({ proxy }: any) => {
  const $nextTick = (afterRender) => {
    const $forceUpdate = (resolve) => {
      if (afterRender) {
        proxy.$forceUpdate()
      }
      resolve(null)
    }
    return (delay = 0) => {
      return new Promise((resolve) => {
        nextTick(() => {
          if (delay > 0) {
            setTimeout(() => {
              $forceUpdate(resolve)
            }, delay)
          } else {
            $forceUpdate(resolve)
          }
        })
      })
    }
  }
  return {
    nextTick: $nextTick(false),
    nextRender: $nextTick(true)
  }
}

export const useRefresh = ({ proxy }: any) => {
  const refresh = async (option?: { condition: () => boolean, second: number, times: number }) => {
    if (option) {
      for (let a = 0; a < option.times; a++) {
        if (option.condition()) {
          proxy.$forceUpdate()
          break
        } else {
          await sleep(option.second * 1000)
        }
      }
    } else {
      proxy.$forceUpdate()
    }
  }
  return refresh
}
