
import {
  defineComponent,
  ref,
  watch,
  getCurrentInstance,
  onMounted,
} from "vue";
import { useCss } from "/@/plugins/theme";
import { useStore } from "vuex";

export default defineComponent({
  props: {
    align: String,
  },
  components: {},
  setup(props, context) {
    const instance = getCurrentInstance();
    const slotComponents = ref([]);
    // onMounted(() => {
    //   const p = props;
    //   const c = context;
    //   const i = instance;
    //   slotComponents.value = instance.slots.default();
    //   console.log(p, c, i);
    //   return () => {
    //     console.log(p);
    //   };
    // });
    const { state } = useStore();
    watch(
      () => state.layout.appendFooterComponent,
      () => {
        slotComponents.value = state.layout.appendFooterComponent();
      }
    );
    const { reactiveCss } = useCss(instance);
    reactiveCss(() => {
      let fixedStyle = {};
      // 对其方式
      let align = {};
      if (props.align === "left") {
        align = {
          justifyContent: "flex-start",
        };
      } else if (props.align === "center") {
        align = {
          justifyContent: "center",
        };
      } else {
        align = {
          justifyContent: "flex-end",
        };
      }
      fixedStyle = {
        ".page-footer-container .page-footer-slot-box": {
          width: "100%",
          padding: "10px",
          ...align,
        },
      };
      return { ...fixedStyle };
    });
    return {
      slotComponents,
    };
  },
});
