
import {
  defineComponent,
  watch,
} from "vue";
import { useStore } from "vuex";
export default defineComponent({
  setup(props, context) {
    const { state } = useStore();
    watch(
      () => state.layout.selectedTag,
      () => {
        context.emit("change", state.layout.selectedTag)
      }
    );
    return {}
  },
});
