import lodash from "lodash";
/**
 * 重写原型方法
 */
declare const Array: any;
declare const String: any;
declare const Number: any;
declare const Object: any;
declare const Promise: any;
const sleep = async (daily) => new Promise((resolve) => setTimeout(() => resolve(), daily));
(() => {
  // Array rewrite
  Object.defineProperty(Array.prototype, 'loopDaily', {
    enumerable: false,
    writable: false,
    configurable: false,
    // eslint-disable-next-line @typescript-eslint/ban-types
    value: function (callback: Function, daily = 5) {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const _this = this;
      const looper = async () => {
        for (const i in _this) {
          const item = _this[i]
          await sleep(daily);
          setTimeout(() => {
            callback(item, i)
          }, 0)
        }
        // eslint-disable-next-line no-useless-return
        return
      }
      // eslint-disable-next-line @typescript-eslint/no-empty-function
      looper().then(() => { });
    }
  })
  Object.defineProperty(Array.prototype, 'indexOfObj', {
    enumerable: false,
    writable: false,
    configurable: false,
    // eslint-disable-next-line @typescript-eslint/ban-types
    value: function (keyOrFunc: string | Function, value?: any) {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const _this = this;
      for (const index in _this) {
        if (typeof keyOrFunc === 'string') {
          if (_this[index][keyOrFunc] === value) {
            return parseInt(index)
          }
        } else {
          if (keyOrFunc.call(_this, _this[index], index)) {
            return parseInt(index)
          }
        }
      }
      return -1
    }
  })
  Object.defineProperty(Array.prototype, 'filterOne', {
    enumerable: false,
    writable: false,
    configurable: false,
    // eslint-disable-next-line @typescript-eslint/ban-types
    value: function (callback: Function) {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const _this = this;
      const filterRes = _this.filter(callback)
      if (filterRes && filterRes.length > 0) {
        return filterRes[0]
      }
      return undefined
    }
  })
  Object.defineProperty(Array.prototype, 'remove', {
    enumerable: false,
    writable: false,
    configurable: false,
    // eslint-disable-next-line @typescript-eslint/ban-types
    value: function (callback: Function) {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const _this = this;
      let index = -1;
      for (const i in _this) {
        if (callback(_this[i], parseInt(i))) {
          index = parseInt(i);
          break;
        }
      }
      if (index === -1) {
        return []
      } else {
        return _this.splice(index, 1)
      }
    }
  })
  Object.defineProperty(Array.prototype, 'removeMore', {
    enumerable: false,
    writable: false,
    configurable: false,
    // eslint-disable-next-line @typescript-eslint/ban-types
    value: function (callback: Function) {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const _this = this;
      let resArray: Array<any> = []
      let skip = false;
      while (!skip) {
        const res = _this.remove(callback)
        if (res.length === 0) {
          skip = true
        } else {
          resArray = [...resArray, ...res]
        }
      }
      return resArray
    }
  })
  // String rewrite
  Object.defineProperty(String.prototype, 'indexOfMuti', {
    enumerable: false,
    writable: false,
    configurable: false,
    value: function (strsOrRegular: Array<string> | RegExp) {
      if (Object.prototype.toString.call(strsOrRegular) === "[object RegExp]") {
        return this.search(strsOrRegular)
      } else {
        const strs = strsOrRegular as Array<string>;
        // eslint-disable-next-line @typescript-eslint/no-inferrable-types
        const reg: RegExp = new RegExp(strs.map((str: string) =>
          [`!`, `$`, `^`, `*`, `(`, `)`, `-`, `+`, `=`, `{`, `}`, `[`, `]`, `|`, `\\`, `/`, `?`, `'`, `"`, `.`].includes(str) ? `\\${str}` : str).join('|'))
        return this.search(reg)
      }
    }
  })
  // String rewrite
  Object.defineProperty(String.prototype, 'remove', {
    enumerable: false,
    writable: false,
    configurable: false,
    value: function (strsOrRegs: (Array<string | RegExp>)) {
      let localStr = String(this)
      for (const index in strsOrRegs) {
        localStr = localStr.replace(strsOrRegs[index], '')
      }
      return localStr
    }
  })
  Object.defineProperty(String.prototype, 'stringCodeAt', {
    enumerable: false,
    writable: false,
    configurable: false,
    value: function () {
      let codeStr = ''
      for (const char of this) {
        const code = char.charCodeAt()
        let codeChar = ''
        if (code >= 0 && code < 10) {
          codeChar = '000' + String(code)
        } else if (code >= 10 && code < 100) {
          codeChar = '00' + String(code)
        } else if (code >= 100 && code < 1000) {
          codeChar = '0' + String(code)
        } else {
          codeChar = '' + String(code)
        }
        codeStr += codeChar
      }
      if (codeStr.length > 0) {
        return parseInt(codeStr)
      } else {
        return 0
      }
    }
  })
})()
