import { Module } from 'vuex'
import { RootState } from '..'

export interface State {
  userName: string,
  isLogin: boolean
}

const user: Module<State, RootState> = {
  state: {
    userName: '',
    isLogin: false
  },
  getters: {
    isLogin: (state) => {
      if (state.userName !== '') {
        return true
      } else {
        return false
      }
    }
  },
  mutations: {
    setUserName: (state, userName) => { state.userName = userName },
    setIsLogin: (state, isLogin) => { state.isLogin = isLogin }
  }
}

export default user
