const usage = 'insert-css: You need to provide a CSS string. Usage: insertCss(cssString[, options]).';

const styleContainers: any = {}

interface Options {
  position?: 'prepend' | 'append' | undefined,
  styleId?: string,
  symbol?: symbol
}

export const createCss = (options: Options = {}) => {
  const { position, styleId, symbol } = options

  const container: any = document.querySelector('head');
  const defaultStyleId = 'useCss'
  const createCssElement = () => {
    styleElement = createStyleElement(styleId || defaultStyleId);
    if (position === 'prepend') {
      container.insertBefore(styleElement, container.childNodes[0]);
    } else {
      container.appendChild(styleElement);
    }
  }

  let styleElement: any = null;
  if (symbol) {
    if (styleContainers[symbol]) {
      styleElement = styleContainers[symbol]
    } else {
      createCssElement()
      styleContainers[symbol] = styleElement
    }
  } else {
    styleElement = container.querySelector(`[styleId=${styleId || defaultStyleId}]`)
    if (styleElement) {
      //
    } else {
      createCssElement()
    }
  }
  return styleElement
}

export const insertCss = (css, styleElement) => {
  if (css === undefined) {
    throw new Error(usage);
  }

  // strip potential UTF-8 BOM if css was read from a file
  if (css.charCodeAt(0) === 0xFEFF) { css = css.substr(1, css.length); }

  // actually add the stylesheet
  if (styleElement.styleSheet) {
    styleElement.styleSheet.cssText = css
  } else {
    styleElement.textContent = css;
  }

  return styleElement;
}

const createStyleElement = (styleId: string) => {
  const styleElement = document.createElement('style');
  styleElement.setAttribute('type', 'text/css');
  styleElement.setAttribute('styleId', styleId);
  return styleElement;
}
